.benchmark-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
    font-family: Arial, sans-serif;
  }
  
  .section {
    background-color: white;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0,0,0,0.1);
    margin-bottom: 20px;
  }
  
  .title {
    font-size: 28px;
    font-weight: bold;
    margin-bottom: 20px;
    text-align: center;
  }
  
  .subtitle {
    font-size: 22px;
    font-weight: bold;
    margin-bottom: 15px;
  }
  
  .scene-title {
    font-size: 18px;
    font-weight: bold;
    margin-top: 20px;
    margin-bottom: 10px;
  }
  
  .upload-button {
    display: inline-block;
    background-color: #3b82f6;
    color: white;
    padding: 8px 16px;
    border-radius: 6px;
    cursor: pointer;
    margin-right: 10px;
  }
  
  .upload-container {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
  }
  
  .filename {
    margin-left: 10px;
  }
  
  .hidden {
    display: none;
  }
  
  .error-message {
    color: #ef4444;
    margin-top: 10px;
  }
  
  .tabs {
    display: flex;
    margin-bottom: 20px;
  }
  
  .tab-button {
    padding: 8px 16px;
    margin-right: 10px;
    border-radius: 6px;
    border: none;
    cursor: pointer;
  }
  
  .tab-button.active {
    background-color: #3b82f6;
    color: white;
  }
  
  .tab-button:not(.active) {
    background-color: #e5e7eb;
  }
  
  .chart-container {
    height: 400px;
    width: 100%;
    margin: 20px 0;
  }
  
  .selector-container {
    margin-bottom: 15px;
  }
  
  .selector-label {
    display: block;
    font-size: 14px;
    font-weight: medium;
    color: #4b5563;
    margin-bottom: 5px;
  }
  
  .scene-selector {
    display: block;
    width: 250px;
    padding: 8px 12px;
    border: 1px solid #d1d5db;
    border-radius: 6px;
    font-size: 14px;
  }
  
  .table-container {
    overflow-x: auto;
    margin-bottom: 20px;
  }
  
  table {
    width: 100%;
    border-collapse: collapse;
  }
  
  th, td {
    padding: 10px;
    text-align: left;
    border-bottom: 1px solid #e5e7eb;
  }
  
  thead th {
    background-color: #f3f4f6;
  }
  
  .metric-name {
    font-weight: medium;
  }
  
  .metric-value {
    text-align: center;
  }
  
  .scene-metrics {
    margin-bottom: 30px;
  }
  
  .good-fps {
    color: green;
    font-weight: bold;
  }
  
  .average-fps {
    color: #f0ad4e;
    font-weight: bold;
  }
  
  .poor-fps {
    color: orange;
    font-weight: bold;
  }
  
  .bad-fps {
    color: red;
    font-weight: bold;
  }
  
  .summary-grid {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 15px;
  }
  
  @media (min-width: 768px) {
    .summary-grid {
      grid-template-columns: repeat(3, 1fr);
    }
  }
  
  .summary-card {
    background-color: #f3f4f6;
    padding: 15px;
    border-radius: 6px;
  }
  
  .summary-label {
    color: #4b5563;
    font-size: 14px;
  }
  
  .summary-value {
    font-size: 24px;
    font-weight: bold;
    margin: 5px 0;
  }
  
  .summary-note {
    color: #6b7280;
    font-size: 12px;
    margin-top: 5px;
  }
  
  .stutter-row {
    background-color: #fee2e2;
  }
  
  .empty-state {
    text-align: center;
    padding: 50px 20px;
  }
  
  .loading {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 300px;
    font-size: 18px;
  }